.outro {
  color: white;
  position: absolute;
  bottom: 5px;
  text-align: center;
  width: 40%;
  left: 30%;
  position: fixed;
}

a {
  color: white;
  text-decoration: underline;
}

.modal-high {
  position: fixed;
  z-index: 1;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  height: 95%;
  width: 95%;
  box-sizing: border-box;
  padding: 1rem;
  z-index: 2;
  background-color: black;
  opacity: 0.8;
  color: white;
  font-weight: 900;
}

.question {
  position: absolute;
  top: 0rem;
  left: 1rem;
  transform: rotate(-40deg);
  color: white;
  font-size: 4rem;
}

.modal > h3 {
  font-size: 1.9rem;
}

.modal > p {
  font-size: 1.5rem;
}

@media screen and (max-width: 1200px) {
  .modal > h3 {
    font-size: 2rem;
  }

  .modal > p {
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 600px) {
  .modal > h3 {
    font-size: 1.4rem;
  }

  .modal > p {
    font-size: 0.6rem;
  }
}
