@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200&display=swap');

:root {
  /* Here is where to change colors to affect the whole site */
  --color-bg: #18182d;
  --color-bg-variant: #2c2c6c;
  --color-bg-variant2: #23237a;
  --color-primary: #5fb8f8;
  --color-primary-variant: #4db5ff66;
  --color-dark: #000000;
}

body {
  background-color: var(--color-bg);
  font-family: 'Manrope', sans-serif;
  margin: 0;
  padding: 0;
  /* overflow: hidden; */
}

.kanban-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  font-size: 4rem;
  margin: 0;
}

.columns-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2.5rem;
  margin: 5px;
}

@media screen and (max-width: 1200px) {
  .columns-container {
    display: grid;
    /* flex-wrap: wrap; */
    grid-template-columns: 1fr 1fr;
    gap: 1.2rem;
  }
  .columns-container > * {
    justify-content: center;
  }
}

@media screen and (max-width: 600px) {
  .columns-container {
    display: grid;

    grid-template-columns: 1fr;
    gap: 1rem;
  }
}

.column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--color-bg-variant2);
  margin: 10px 10px;
  border-radius: 5px;
  min-width: 300px;
  border-radius: 2rem;
  border: 1px solid transparent;
}

.column:hover {
  border-color: var(--color-primary-variant);
  background: var(--color-bg-variant);
}

.column h2 {
  padding: 10px;
  margin: 0;
  text-align: center;
  color: white;
}

.task-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding: 0;
  flex-grow: 1;
}

.task {
  min-width: 200px;
  background-color: var(--color-primary-variant);
  color: white;
  border: 1px solid var(--color-dark);
  border-radius: 1rem;
  margin: 10px;
  padding: 10px;
  cursor: grab;
  list-style-type: none;
  text-align: center;
}

.task:hover {
  color: white;
  border: 1px solid transparent;
}

.add-task-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 3px;
  /* flex-grow: 1; */
}

.add-task-container input[type='text'] {
  flex: 1;
  padding: 5px;
  width: 17rem;
  max-height: 2rem;
  border-radius: 1rem;
}

.add-task-container button {
  background-color: #4caf50;
  color: white;
  border: none;
  margin: 1rem;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

.add-task-container button:hover {
  background-color: #3e8e41;
}

.delete-task {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 0.3rem;
  border-radius: 2rem;
  cursor: pointer;
  float: right;
}

.delete-task:hover {
  background-color: #d32f2f;
}

.delete-column {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 10px;
}

.delete-column:hover {
  background-color: #d32f2f;
}

.add-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* flex-wrap: wrap; */
  background: var(--color-bg-variant);
  margin: 10px 10px;
  border-radius: 5px;
  min-width: 300px;
  flex-grow: 1;
  border-radius: 2rem;
  border: 1px solid transparent;
}

.add-column-input[type='text'] {
  width: 17rem;
  padding: 5px;
  border-radius: 1rem;
}

.add-column-button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 10px;
}

.add-column button:hover {
  background-color: #3e8e41;
}
